import { RendererJSAPI } from '../../../../lib'
import { getJSONFromURL } from 'njscore';
import * as shapefile from 'shapefile'; // https://www.npmjs.com/package/shapefile

import * as turf from '@turf/turf'

// import Shp from 'shpjs';

export class Solution extends RendererJSAPI {

    locations: number[][] = [];
    colors: string[] = [];
    names: string[] = [];

    shape;

    constructor(view: any) {
        super(view);
        // TODO
        // You code goes here for one time operation
        this.zoom(3)


        const uploadFile = document.createElement("INPUT");
        this.hostDiv.appendChild(uploadFile);
        uploadFile.style.position = 'absolute'
        uploadFile.setAttribute("type", "file");
        uploadFile.onchange = (evt) => {
            // @ts-ignore
            const file = evt.target.files[0];
            const reader = new FileReader();
            console.log(file)

            reader.onload =  (event) =>{
                const arrayBuffer = event.target.result;
                // @ts-ignore
                const uint8Array = new Uint8Array(arrayBuffer);
                shapefile.open(uint8Array)
                  .then(source => source.read().then((result) => {
                    if (result.done) return;
                    this.shape = {
                        features: [result.value],
                        type: 'FeatureCollection',
                    }
                    const cp = turf.center(this.shape)
                    this.center(cp.geometry.coordinates[0], cp.geometry.coordinates[1])
                    this.start()

                  })
                  ).catch(error => console.error(error.stack));
            };
            reader.readAsArrayBuffer(file)
        }

        

        const path = 'https://raw.githubusercontent.com/NamjuLee/data/master/shape/cb_2018_us_county_5m/cb_2018_us_county_5m.shp';
        this.importAsJSON(path)
    }
    public async importAsJSON(path: string) {
        const geojson = await shapefile.read(path)
        this.shape = geojson
        console.log(geojson)
        this.start();
    }

    public render(ctx: CanvasRenderingContext2D): void {
        // TODO
        // You code goes here for the rending loop

        if (this.shape) {

            for (let i = 0; i < this.shape.features.length; ++i) {
                const feature = this.shape.features[i]
                if (feature.geometry.type === 'Polygon') {
                    renderPolygon(ctx, feature.geometry, this.toScreen)
                }
            }

        }
    }
}
const renderPoint = (ctx: CanvasRenderingContext2D, x: number, y: number, t: number, color: string) => {
    ctx.fillStyle = color;
    ctx.strokeStyle = '#fff';
    ctx.lineWidth = 0.75;
    ctx.beginPath();
    ctx.arc(x, y, (Math.sin(t * 0.8) + 4) * 2, 0, Math.PI * 2);
    ctx.closePath();
    ctx.fill();
    ctx.stroke();
};
const renderText = (ctx: CanvasRenderingContext2D, x: number, y: number, name: string, color: string) => {
    ctx.strokeStyle = color;
    ctx.lineWidth = 0.5;
    ctx.strokeText(name, x + 13, y);
};


const renderPolygon = (ctx, geometry, toScreen, color = '#f00', dash = false) => {
    for (let i = 0; i < geometry.coordinates.length; ++i) {
        const coordinates = geometry.coordinates[i]
        // console.log(coordinates)
        let sp = toScreen(coordinates[0][0], coordinates[0][1]);
        ctx.beginPath()
        ctx.moveTo(sp[0], sp[1])


        for (let j = 1; j < coordinates.length; ++j) {
            // console.log(coordinates[j]);
            sp = toScreen(coordinates[j][0], coordinates[j][1]);
            // console.log(sp)
            ctx.lineTo(sp[0], sp[1]);
        }
        ctx.closePath();
        ctx.strokeStyle = color;
        ctx.stroke();
    }
}